.js body .shapeTransit1,
.js body .shapeTransit2 {
  height: 100%;
  width: 100%;
  bottom: -100%;
  left: 0;
  position: fixed;
}

.js body .shapeTransit1 {
  background: #d8dde6;
  z-index: 20000;
}

.js body .shapeTransit1::after {
  background-image: url(../img/logo_gray.png);
  background-size: cover;
  width: 290px;
  height: 50px;
  position: absolute;
  left: 50%;
  top: 40%;
  display: block;
  transform: translate(-50%, -50%);
}

.js body .shapeTransit2 {
  background: #f5f0ec;
  z-index: 21000;
}

.js body > .loader {
  position: fixed;
  left: 0;
  right: 0;
  transition: height 0.4s 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  width: 100%;
  height: 100vh;
  background: #d8dde6;
  overflow: hidden;
  z-index: 999;
}

.js body > .loader .loader__wrap {
  position: absolute;
  left: 50%;
  top: 50%;
  display: block;
  transform: translate(-50%, -50%);
  width: 260px;
  height: 230px;
}

.js body > .loader .loader__wrap::after {
  background-image: url(../img/logo_gray.png);
  background-size: cover;
  width: 290px;
  height: 50px;
  animation: pulse 0.9s alternate infinite;
}

@media only screen and (max-width: 767px) {
  .js body > .loader .loader__wrap::after {
    width: 250px;
    height: 40px;
  }
}

@keyframes pulse {
  from {
    transform: scale(1.6);
  }
  to {
    transform: scale(0.8);
    opacity: 0.5;
  }
}

.js body {
  overflow: hidden;
}

.js body.is-loaded {
  overflow: initial;
}

.js body.is-loaded > .loader {
  height: 0;
}

.page__title .page__title-text {
  position: relative;
}

.page__title .page__title-text:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transform: scale3d(0, 1, 1);
  z-index: 100;
}

.is-animate .page__title .page__title-text:before {
  animation-name: textDiscover;
  animation-duration: 1s;
  animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
}

main {
  position: relative;
}

main:before {
  content: '';
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transform: scale3d(0, 1, 1);
  z-index: 100;
}

.is-animate main:before {
  animation-name: blockDiscover;
  animation-duration: 1.2s;
  animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
}

@keyframes textDiscover {
  0% {
    transform: scale3d(1, 0, 1);
    transform-origin: center bottom;
  }
  40% {
    transform: scale3d(1, 1, 1);
    transform-origin: center bottom;
  }
  60% {
    transform: scale3d(1, 1, 1);
    transform-origin: center top;
  }
  100% {
    transform: scale3d(1, 0, 1);
    transform-origin: center top;
  }
}

@keyframes blockDiscover {
  0% {
    transform: scale3d(0, 1, 1);
    transform-origin: left center;
  }
  40% {
    transform: scale3d(1, 1, 1);
    transform-origin: left center;
  }
  60% {
    transform: scale3d(1, 1, 1);
    transform-origin: right center;
  }
  100% {
    transform: scale3d(0, 1, 1);
    transform-origin: right center;
  }
}

.sidebar .menu > li > a::after {
